#header{
  margin: 0 0 10px 0;
  font-size: 48px;
  color: black;
  font-weight: 600;

}
#header .brand{
  color: rgb(71, 71, 235);
}
#header h2{
  color: #484848;
}
#header .animated{
  animation: left-right 1.5s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes left-right{
 0%{
   transform: translate(10px);
 }
 100%{
   transform: translate(-10px);
 } 
}
@keyframes left-right {
  0%{
    transform: translate(20px);
  }
  100%{
    transform: translate(-20px);
  } 
  
}
.col-md-4{
  padding-bottom: 100px;
}
.card-img-top{
  width: 120px;
}
.card{
 
  margin: 6%;
	transition: all 0.4s cubic-bezier(0.175,0.885,0,1);
	background-color: #fff;
	width: 70%;
  height: 100%;
	border-radius: 12px;
	box-shadow: 0px 13px 10px -7px rgba(0,0,0,0.1);
	float: left;
}
.card-img-top{
 
  margin-left: 70px;
}
